import React from "react";
import dayjs from "dayjs";
import { observer, inject } from "mobx-react";
import { Button, Toast, Modal } from "antd-mobile";
import SubPage from "@/components/SubPage";
import HyList from "@/components/HyList/List";

import icon1 from "@/assets/imgs/meeting/message_icon1.png";
import icon2 from "@/assets/imgs/meeting/message_icon2.png";
import icon3 from "@/assets/imgs/meeting/message_icon3.png";
import icon4 from "@/assets/imgs/meeting/message_icon4.png";
import icon5 from "@/assets/imgs/meeting/message_icon5.png";
import icon6 from "@/assets/imgs/meeting/message_icon6.png";
import icon7 from "@/assets/imgs/meeting/message_icon7.png";
import icon8 from "@/assets/imgs/meeting/message_icon8.png";

import { IMembers } from "../../interface";
import { toJS } from "mobx";
import storage from "@/utils/storage";

const alert = Modal.alert;
require("./style.scss");

const { InputItem, TextareaItem, PickerItem, DatePickerItem, JumpItem } = HyList;
const week = ["日", "一", "二", "三", "四", "五", "六"];

interface IState {
  heigth: number;
  visible: boolean;
}

@inject("userInfoStore")
@inject("meetingManagementDetailStore")
@observer
class MeetingManagementDetail extends React.Component<IProps, IState> {
  componentDidMount() {
    const {
      userInfoStore: { fetchUserInfo }
    } = this.props;
    fetchUserInfo();
    const {
      match: { params },
      meetingManagementDetailStore: { updateDetail, fetchDetail, updatePersonStr, updateOnlyRead, reset, setPageType }
    } = this.props;
    if (!params) return;
    let meetingDetail: any = storage.getItem("meetingDetail");
    let meetinglocation = storage.getItem("meetinglocation");
    let selectMeetingRoom = storage.getItem("selectMeetingRoom");
    let meetingPerson = storage.getItem("meetingPerson");
    meetingDetail = meetingDetail ? JSON.parse(meetingDetail) : null;
    meetinglocation = meetinglocation ? JSON.parse(meetinglocation) : null;
    selectMeetingRoom = selectMeetingRoom ? JSON.parse(selectMeetingRoom) : null;
    meetingPerson = meetingPerson ? JSON.parse(meetingPerson) : null;

    if (params.id !== "add") {
      // 详情
      setPageType("edit");
      if (meetingDetail && meetingDetail.id === params.id) {
        updateDetail(meetingDetail);
        storage.removeItem("meetingDetail");
      } else {
        fetchDetail(params.id);
      }
    } else {
      // 新增
      setPageType("add");
      if (meetingDetail) {
        updateDetail(meetingDetail);
        storage.removeItem("meetingDetail");
      }
    }
    this.onLocationStateChange(meetinglocation, selectMeetingRoom, meetingPerson);
    if (!meetingDetail && !meetinglocation && !selectMeetingRoom && !meetingPerson) {
      reset();
      if (params.id !== "add") {
        setPageType("edit");
      } else {
        setPageType("add");
      }
    }
  }

  onLocationStateChange = (meetinglocation: any, selectMeetingRoom: any, meetingPerson: any) => {
    const {
      meetingManagementDetailStore: { updateLoaction, updatePerson, updateMeeting, updatePersonStr, updateOnlyRead }
    } = this.props;
    if (meetinglocation) {
      // 选地图
      updateLoaction(meetinglocation);
      storage.removeItem("meetinglocation");
    }
    if (meetingPerson) {
      // 选人
      updatePerson(meetingPerson);
      storage.removeItem("meetingPerson");
      updatePersonStr();
    }
    if (selectMeetingRoom) {
      // 选会议室
      updateMeeting(selectMeetingRoom);
      storage.removeItem("selectMeetingRoom");
      updateOnlyRead();
    }
  };

  formatDate = (start: any, end?: any) => {
    return `${dayjs(start).format("YYYY年MM月DD日")} 周${week[dayjs(start).day()]} ${dayjs(start).format("HH:mm")}${
      end ? "-" + dayjs(end).format("HH:mm") : ""
    }`;
  };

  onTimeChange = (type: string, date: string) => {
    const {
      meetingManagementDetailStore: { detail = {}, onDetailChange }
    } = this.props;
    const { startTime, endTime } = detail || {};
    if (type === "startTime" && (!endTime || (endTime && dayjs(date).isBefore(dayjs(endTime))))) {
      if (date && endTime && !dayjs(date).startOf("day").isSame(dayjs(endTime).startOf("day"))) {
        Toast.info("开始时间和结束时间需在同一天");
        return;
      }
      onDetailChange("startTime", date);
    } else if (type === "endTime" && (!startTime || (startTime && dayjs(date).isAfter(dayjs(startTime))))) {
      if (date && startTime && !dayjs(date).startOf("day").isSame(dayjs(startTime).startOf("day"))) {
        Toast.info("开始时间和结束时间需在同一天");
        return;
      }
      onDetailChange("endTime", date);
    } else {
      Toast.info("开始时间需小于结束时间", 1);
    }
  };

  jumpSelectLocation = (latlng: any) => {
    const {
      match: { params },
      meetingManagementDetailStore: { detail }
    } = this.props;
    storage.setItem("meetingDetail", JSON.stringify(detail));
    this.props.history.push({
      pathname: "/choose_address",
      state: {
        pathname: `/meeting-management/${params.type}/${params.id || "add"}`,
        params: {
          latlng
        }
      }
    });
  };

  cancel = () => {
    const {
      history,
      match: { params },
      meetingManagementDetailStore: { cancelMeeting }
    } = this.props;
    if (params && params.id) {
      alert("提示", "您确定取消会议吗？", [
        { text: "否" },
        {
          text: "是",
          onPress: () => {
            cancelMeeting(params.id, () => {
              setTimeout(() => {
                history.goBack();
              }, 1000);
            });
          }
        }
      ]);
    }
  };

  componentWillUnmount() {
    const {
      meetingManagementDetailStore: { reset }
    } = this.props;
    reset();
  }

  returnLocationSection = () => {
    const {
      match: { params },
      userInfoStore: { userMemberInfo },
      meetingManagementDetailStore: { detail = {}, onDetailChange, onlyRead }
    } = this.props;
    const { endTime, roomId, startTime, latitude, longitude, roomName, nowAddress, specificAddress } = detail || {};

    if (params && params.type) {
      if (params.type === "inside") {
        return (
          <div className="meeting-management-detail-section">
            <JumpItem
              icon={icon4}
              value={roomName}
              placeholder="请选择会议室"
              arrow="horizontal"
              disabled={onlyRead}
              onClick={() => {
                storage.setItem("meetingDetail", JSON.stringify(detail));
                this.props.history.push({
                  pathname: `/meeting-selectRoom`,
                  state: Object.assign(
                    {},
                    {
                      pathname: `/meeting-management/${params.type}/${params.id || "add"}`
                    },
                    { meetingsponsor: userMemberInfo.id, meetingId: detail.id },
                    roomId &&
                      startTime &&
                      endTime && {
                        selectMeeting: {
                          id: roomId,
                          startTime,
                          endTime
                        }
                      }
                  )
                });
              }}
            />
            <InputItem
              icon={icon3}
              disabled={true}
              value={startTime && endTime ? this.formatDate(startTime, endTime) : ""}
              placeholder="会议时间起止"
            />
          </div>
        );
      } else if (params.type === "outside") {
        const now = dayjs().valueOf();
        const startHour = dayjs().startOf("hour").valueOf();
        const minDate = now - startHour < 1800000 ? dayjs(startHour + 1800000 * 1).toDate() : dayjs(startHour + 1800000 * 2).toDate();
        return (
          <div className="meeting-management-detail-section">
            <DatePickerItem
              icon={icon3}
              placeholder="请选择会议开始时间"
              value={startTime && dayjs(startTime).toDate()}
              formatValue={this.formatDate}
              disabled={onlyRead}
              minDate={minDate}
              minuteStep={30}
              onChange={(field: any, date: any) => {
                // this.onTimeChange("startTime", dayjs(date).format());
                onDetailChange("startTime", date);
              }}
            />
            <DatePickerItem
              icon={icon3}
              placeholder="请选择会议结束时间"
              value={endTime && dayjs(endTime).toDate()}
              formatValue={this.formatDate}
              disabled={onlyRead}
              minDate={minDate}
              minuteStep={30}
              onChange={(field: any, date: any) => {
                // this.onTimeChange("endTime", dayjs(date).format());
                onDetailChange("endTime", date);
              }}
            />
            <JumpItem
              icon={icon4}
              value={nowAddress}
              placeholder="请选择会议地点"
              arrow="horizontal"
              disabled={onlyRead}
              onClick={() => {
                if (!longitude || !latitude) {
                  this.props.wxApi.location.getLocation({
                    type: "gcj02",
                    success: (res: any) => {
                      const lat = res.latitude;
                      const lng = res.longitude;
                      this.jumpSelectLocation({
                        lat,
                        lng
                      });
                    }
                  });
                  // this.jumpSelectLocation({
                  //   lat: 22.978066,
                  //   lng: 113.372803
                  // });
                } else {
                  this.jumpSelectLocation({
                    lat: latitude,
                    lng: longitude
                  });
                }
              }}
            />
            <InputItem
              field="specificAddress"
              value={specificAddress || ""}
              placeholder="请输入详细地址"
              disabled={onlyRead}
              onChange={onDetailChange}
            />
          </div>
        );
      }
    }
  };

  render() {
    const {
      userInfo,
      history,
      match: { params },
      userInfoStore: { baseUserInfo, userMemberInfo },
      meetingManagementDetailStore: {
        title,
        locationRange,
        locationRange_kv,
        timeRange,
        timeRange_kv,
        detail,
        onDetailChange,
        onlyRead,
        submit
        // memberName,
      }
    } = this.props;
    const {
      avoidLaterTime,
      checkinDelay,
      checkinEarly,
      meetingDescribe,
      meetingSponsor,
      name,
      scope,
      startTime,
      personStr,
      selectMembers = [],
      endTime
    } = detail || {};
    const { memberName } = baseUserInfo || {};
    const workNumber = (userMemberInfo || {}).WorkNumber;
    const hrNumberId = (userMemberInfo || {}).id;
    const orgId = (userMemberInfo || {}).OrgId;
    return (
      <SubPage
        history={history}
        className="meeting-management-detail-page"
        title={title}
        bgColor="#eee"
        rightContent={
          params && params.id && params.id !== "add" && endTime && dayjs(endTime).isAfter(dayjs()) && <a onClick={this.cancel}>取消会议</a>
        }
      >
        <HyList>
          <div className="meeting-management-detail-section">
            <InputItem icon={icon1} field="name" value={name || ""} placeholder="请输入会议名称" disabled={onlyRead} onChange={onDetailChange} />
            <TextareaItem
              icon={icon2}
              rows={3}
              field="meetingDescribe"
              value={meetingDescribe || ""}
              placeholder="请输入描述"
              disabled={onlyRead}
              onChange={onDetailChange}
            />
          </div>
          {this.returnLocationSection()}
          <div className="meeting-management-detail-section">
            <InputItem icon={icon5} disabled={onlyRead} value={`发起人:${meetingSponsor || memberName || userInfo.userName || ""}`} />
            <JumpItem
              icon={icon6}
              value={personStr}
              placeholder="请选择参与人"
              arrow="horizontal"
              onClick={() => {
                storage.setItem("meetingDetail", JSON.stringify(detail));
                if (params && params.id && params.id !== "add" && dayjs(startTime).isBefore(dayjs())) {
                  this.props.history.push({
                    pathname: "/choose_person/state",
                    state: {
                      pathname: `/meeting-management/${params.type}/${params.id || "add"}`,
                      checkedIn: toJS(
                        selectMembers.filter((item: IMembers) => Number(item.signType) === 1),
                        {
                          recurseEverything: true
                        }
                      ),
                      notCheckedIn: toJS(
                        selectMembers.filter((item: IMembers) => Number(item.signType) === 0),
                        {
                          recurseEverything: true
                        }
                      )
                    }
                  });
                } else {
                  this.props.history.push({
                    pathname: "/choose_person",
                    state: {
                      pathname: `/meeting-management/${params.type}/${params.id || "add"}`,
                      selected: selectMembers.map((item: any) => ({
                        memberId: item.confereeId,
                        name: item.confereeName,
                        // position: item,
                        workNumber: item.workNumber
                      }))
                    }
                  });
                }
              }}
            />
          </div>
          <div className="meeting-management-detail-section">
            {params.type && params.type === "outside" && (
              <PickerItem
                icon={icon7}
                data={locationRange}
                cols={1}
                values={[scope]}
                title="有效签到定位位置范围"
                extra={locationRange_kv[scope]}
                arrow="horizontal"
                disabled={onlyRead}
                onChange={(value: any) => {
                  if (value && value[0]) {
                    onDetailChange("scope", value[0]);
                  }
                }}
              />
            )}
            <InputItem disabled={true} value="有效签到时间段" />
            <PickerItem
              data={timeRange}
              cols={1}
              values={[checkinEarly]}
              placeholder="会议开始前"
              extra={timeRange_kv[checkinEarly]}
              arrow="horizontal"
              disabled={onlyRead}
              onChange={(value: any) => {
                if (value && value[0]) {
                  onDetailChange("checkinEarly", value[0]);
                }
              }}
            />
            <PickerItem
              data={timeRange}
              cols={1}
              values={[checkinDelay]}
              placeholder="会议开始后"
              extra={timeRange_kv[checkinDelay]}
              arrow="horizontal"
              disabled={onlyRead}
              onChange={(value: any) => {
                if (value && value[0]) {
                  onDetailChange("checkinDelay", value[0]);
                }
              }}
            />
            <PickerItem
              icon={icon8}
              data={timeRange}
              cols={1}
              values={[avoidLaterTime]}
              title="迟到缓冲时间"
              extra={timeRange_kv[avoidLaterTime]}
              arrow="horizontal"
              disabled={onlyRead}
              onChange={(value: any) => {
                if (value && value[0]) {
                  onDetailChange("avoidLaterTime", value[0]);
                }
              }}
            />
          </div>
        </HyList>
        <div className="meeting-management-detail-handle">
          <Button
            type="primary"
            onClick={() => {
              !onlyRead &&
                submit({ workNumber, hrNumberId, orgId }, params, () => {
                  setTimeout(() => {
                    history.goBack();
                  }, 1000);
                });
            }}
            disabled={onlyRead}
          >
            {(() => {
              if (params && params.id) {
                if (params.id === "add") {
                  return "保存";
                } else if (startTime && endTime) {
                  if (dayjs(endTime).isBefore(dayjs())) {
                    return "会议已结束";
                  }
                  if (dayjs(startTime).isBefore(dayjs()) && dayjs(endTime).isAfter(dayjs())) {
                    return "保存";
                  }
                  return "保存";
                }
              }
            })()}
          </Button>
        </div>
      </SubPage>
    );
  }
}

export default MeetingManagementDetail;
