import React, { useContext } from 'react';
import { TokenContext } from '@/pages/_internal/app/middlewares/AppAuthorized';
import WxApiContext from '@/service/wx-api';
import Detail from './detail';

const managementDetail = (props: any) => {
    const tokenContext = useContext(TokenContext);
    const wxContext: any = useContext(WxApiContext);
    const { wxApi } = wxContext;
    const url = window.location.href;
    const api = wxApi(url);
    return <Detail {...props} userInfo={tokenContext} wxApi={api} />;
};

export default managementDetail;
